.menu {
	background-color: lightgray;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
}

.menu__subMenu {
    -ms-flex-positive: 1;
        flex-grow: 1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
}

.menu__choice {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-item-align: start;
        align-self: flex-start;
    -ms-flex-align: stretch;
        align-items: stretch;
    list-style: none;
    margin: 0.25em;
    padding: 0;
    border: solid grey 1px;
    border-radius: 0.25em;
}

.menu__selector {
    cursor: pointer;
    margin: 0;
    padding: 0.25em;
}

.menu__selector:not(:first-child) {
    border-left: solid grey 1px;
}

.menu__selector--active {
    color: white;
}

@media (max-width:500px) {
    .menu {
        -ms-flex-direction: column;
            flex-direction: column;
    }
}

@media (min-width:501px) and (max-width:900px) {
    .menu__choice {
        -ms-flex-direction: column;
            flex-direction: column;
    }

    .menu__selector:not(:first-child) {
        border-left: none;
        border-top: solid grey 1px;
    }
}


