.App {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;

  position: fixed;
  overflow: hidden;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.dungeonDisplay {
	background-color: #ccc;
	-ms-flex-positive: 1;
	    flex-grow: 1;

	display: -ms-flexbox;

	display: flex;
	overflow: auto;
}

.dungeonDisplay > canvas {
	margin: auto;
	background-color: white;
	border: solid 0.5em #666;
	border-radius: 0.5em;
}


